@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap");

#out {
  -webkit-box-align: center;
  align-items: center;
  background: #212121;
  display: -webkit-box;
  display: flex;
  font-family: "Roboto", sans-serif;
  height: 100vh;
  -webkit-box-pack: center;
  justify-content: center;
}
.just {
  display: flex;
  align-items: center;
  justify-content: center;
}
.joinbutton {
  margin: 5px;
  padding: 15px 25px 15px 25px;
  text-align: center;
  text-transform: uppercase;
  color: #fafafa;
  font-weight: 700;
  background-color: #7b5dfa;
  border: none;
  border-radius: 30px;
  transition: 0.2s;
  border: #6744f9;
}

.joinbutton:hover {
  /* background-color: #b35dfa; */
  /* border: 1px solid #dcb2ff; */
  border-radius: 20px;
  color: #fafafa;
}
.contact-wrapper {
  background: #fff;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.15);
  border-radius: 0.75em;
  padding: 2em;
  width: 25em;
}

.login-cta > h2 {
  color: #3e3f5e;
  font-size: 1.75rem;
  text-align: center;
}
form {
  margin: 2em 0;
}
form > .form-row {
  display: -webkit-box;
  display: flex;
  margin: 0.75em 0;
  position: relative;
}
form > .form-row > span {
  background: #fff;
  color: #adafca;
  display: inline-block;
  font-weight: 400;
  left: 1em;
  padding: 0 0.5em;
  position: absolute;
  pointer-events: none;
  -webkit-transform: translatey(-50%);
  transform: translatey(-50%);
  top: 50%;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
form > .form-row > input,
form > .form-row > button {
  border-radius: 0.5em;
  font-family: inherit;
  padding: 1em 0.5em;
  width: 100%;
}
form > .form-row > input {
  font-weight: bold;
  -webkit-transition: 100ms ease all;
  transition: 100ms ease all;
  width: 100%;
}
form > .form-row > input[type="text"],
form > .form-row > input[type="password"] {
  border: 0.075em solid #ddd;
}
form > .form-row > input:valid + span {
  top: 0;
  font-size: 0.9rem;
}
form > .form-row > input:invalid + span {
  top: 50%;
}
form > .form-row > input:focus + span {
  top: 0;
}
form > .form-row > input:required {
  box-shadow: none;
}
form > .form-row > input:focus {
  border-color: #7b5dfa;
  outline: none;
}
form > .form-row > input:focus:invalid {
  box-shadow: none;
  top: 50%;
}
form > .form-row > input:focus:valid {
  top: 0;
}
form > .form-row > button {
  background-color: #7b5dfa;
  border: 0.1em solid #7b5dfa;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}
form > .form-row > button:focus {
  border: 0.1em solid #532cf8;
  outline: none;
}
form > .form-row > button:hover {
  background-color: #6744f9;
}
