.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #1a1a1d;
}

.container {
  background: #fff;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.15);
  border-radius: 0.75em;
  padding: 2em;
  width: 25em;
  background-color: rgb(19, 19, 19);
  font-family: "Roboto";
  font-size: 12pt;
  /* display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
  border-radius: 8px;
  height: 60%;
  width: 35%; */
}

@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
    background-color: rgb(19, 19, 19);
    font-family: "Roboto";
    font-size: 12pt;
  }

  .container {
    width: 100%;
    height: 100%;
    background-color: rgb(19, 19, 19);
    font-family: "Roboto";
    font-size: 12pt;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 120%;
    background-color: rgb(19, 19, 19);
    font-family: "Roboto";
    font-size: 12pt;
  }
}
