.messageBox {
  background: #f3f3f3;
  border-radius: 8px;
  padding: 0px 10px 0px 10px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;

  float: left;
  margin-top: 4px;
  margin-bottom: 8px;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0px;
  margin-top: 8px;
}

.sentText {
  font-weight: bold;
  /* font-family: Helvetica; */
  margin-top: 8px;
  margin-bottom: 4px;
  padding: 0px;
  color: #00ccff;
  /* letter-spacing: ; */
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: #353535;
}

.backgroundBlue {
  background: #003797;
}

.backgroundLight {
  background: #292929;
}
.mt-8 {
  margin-top: 8px;
}
