@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap);
.messageBox {
  background: #f3f3f3;
  border-radius: 8px;
  padding: 0px 10px 0px 10px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;

  float: left;
  margin-top: 4px;
  margin-bottom: 8px;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0px;
  margin-top: 8px;
}

.sentText {
  font-weight: bold;
  /* font-family: Helvetica; */
  margin-top: 8px;
  margin-bottom: 4px;
  padding: 0px;
  color: #00ccff;
  /* letter-spacing: ; */
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: #353535;
}

.backgroundBlue {
  background: #003797;
}

.backgroundLight {
  background: #292929;
}
.mt-8 {
  margin-top: 8px;
}

.messages {
  padding: 5% 0;
  overflow: auto;
  flex: auto;
}

.infoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #003ca3;
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;
}

.leftInnerContainer {
  flex: 0.7 1;
  display: flex;
  align-items: center;
  margin-left: 5%;
  color: white;
}

.rightInnerContainer {
  display: flex;
  flex: 0.5 1;
  justify-content: flex-end;
  margin-right: 7%;
