}

body {
  align-items: center;
  background: #212121;
  display: flex;
  font-family: "Roboto", sans-serif;
  height: 100vh;
  justify-content: center;
}
.form {
  display: flex;
  /* border-top: 2px solid #d3d3d3; */
}

.input {
  border: none;
  border-radius: 30px 0px 0px 30px;
  padding: 5% 5% 5% 10%;
  width: 80%;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #4c00ff;
  padding: 20px 20px 20px 10px;
  display: inline-block;
  border: none;

  border-radius: 0px 30px 30px 0px;
  width: 20%;
}

.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #1a1a1d;
}

.container {
  background: #fff;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.15);
  border-radius: 0.75em;
  padding: 2em;
  width: 25em;
  background-color: rgb(19, 19, 19);
  font-family: "Roboto";
  font-size: 12pt;
  /* display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
