@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
    background-color: rgb(19, 19, 19);
    font-family: "Roboto";
    font-size: 12pt;
  }

  .container {
    width: 100%;
    height: 100%;
    background-color: rgb(19, 19, 19);
    font-family: "Roboto";
    font-size: 12pt;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 120%;
    background-color: rgb(19, 19, 19);
    font-family: "Roboto";
    font-size: 12pt;
  }
}

#out {
